<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="rolelist" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template v-slot="scope">
            <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '','vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for  -->
                <el-row :class="[i2 === 0 ? '' : 'bdtop','vcenter']" v-for="(item2, i2) in item1.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row, item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning" v-for="item3 in item2.children" :key="item3.id" closable @close="removeRightById(scope.row, item3.id)">
                      {{item3.authName}}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template v-slot="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)"></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="rolesdelete(scope.row.id)"></el-button>
            <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" @close="addDislogClosed">
      <el-form :model="addRolesForm" ref="addRolesForm" label-width="100px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addRolesForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addRolesForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRolesUser"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%">
      <el-form :model="editRolesForm" ref="editFormRef" label-width="100px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editRolesForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editRolesForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editFormInfo"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="setRightDialogVisible" width="50%" @close="setRightDialogClosed">
      <!--  -->
      <el-tree :data="rightslist" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      rolelist: [],
      // 
      rightslist: [],
      // 
      setRightDialogVisible: false,
      // 
      addDialogVisible: false,
      // 
      editDialogVisible: false,
      addRolesForm: {
        roleName: '',
        roleDesc: ''
      },
      editRolesForm: {},
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // id
      defKeys: [],
      // ID
      rolesId: ''
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    //
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error(' !')
      }
      this.rolelist = res.data
    },
    // 
    addRolesUser() {
      this.$refs.addRolesForm.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post('roles', this.addRolesForm)
        if (res.meta.status !== 201) {
          return this.$message.error('!')
        }
        this.$message.success('!')
        this.getRolesList()
        this.addDialogVisible = false
      })
      // console.log(this.addRolesForm)
    },
    // 
    addDislogClosed() {
      this.$refs.addRolesForm.resetFields()
    },
    // 
    async rolesdelete(id) {
      const confirmRusult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch((err) => err)
      if (confirmRusult !== 'confirm') {
        return this.$message.info('')
      }
      this.$http.delete('roles/' + id).then((res) => {
        const { data: response } = res
        if (response.meta.status !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getRolesList()
      })
    },
    // 
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.editRolesForm = res.data
      this.editDialogVisible = true
    },
    editFormInfo() {
      this.$refs.editFormRef.validate((valid) => {
        if (!valid) return
        this.$http
          .put('roles/' + this.editRolesForm.roleId, {
            roleName: this.editRolesForm.roleName,
            roleDesc: this.editRolesForm.roleDesc
          })
          .then((res) => {
            if (res.data.meta.status !== 200) {
              return this.$message.error('!')
            }
            this.$message.success('!')
            this.getRolesList()
          })
        this.editDialogVisible = false
      })
    },
    // ID
    async removeRightById(role, rightId) {
      //
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info(' !')
      }
      // 
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status !== 200) {
        return this.$message.error(' !')
      }
      // 
      role.children = res.data
    },
    // 
    async showSetRightDialog(role) {
      this.roldId = role.id
      // 
      const { data: res } = await this.$http.get('rights/tree')
      //
      if (res.meta.status !== 200) {
        return this.$message.error(' !')
      }
      //
      // data
      this.rightslist = res.data

      // id
      this.getLeafKeys(role, this.defKeys)

      this.setRightDialogVisible = true
    },
    //  id defKeys
    getLeafKeys(node, arr) {
      // nodechildren
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach((item) => {
        this.getLeafKeys(item, arr)
      })
    },
    // 
    setRightDialogClosed() {
      this.defKeys = []
    },
    // 
    async allotRights() {
      const keys = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roldId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) {
        return res.$message.error(' !')
      }
      this.$message.success(' !')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>