<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table class="treeTable" show-index index-text="#" border :data="catelist" :columns="columns" :selection-type="false" :expand-type="false">
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color:lightgreen"></i>
          <i class="el-icon-error" v-else style="color:red"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template v-slot:opt="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="showeditCateDialog(scope.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeCate(scope.row.cat_id)"></el-button>
        </template>
      </tree-table>
      <!--  -->
      <el-pagination class="bottompage" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[1, 5, 10, 15]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addCateDialogVisible" width="50%" @close="addCateDialogClosed">
      <!--  -->
      <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
        <el-form-item label=": " prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label=": ">
          <!-- options  -->
          <!-- props   -->
          <el-cascader clearable change-on-select expandTrigger="hover" v-model="selecteKeys" :options="parentCateList" :props="{
              expandTrigger: 'hover',
              ...cascaderProps,
              checkStrictly: 'true'}" @change="parentCateChanged"></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editCateDialogVisbel" width="50%">
      <el-form :model="editCate" :rules="editCateRules" ref="editCateRef" label-width="100px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editCate.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisbel = false"> </el-button>
        <el-button type="primary" @click="editCateInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      catelist: [],
      // 
      total: 0,
      // table
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'isok'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'order'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'opt'
        }
      ],
      // 
      addCateDialogVisible: false,
      // 
      addCateForm: {
        // 
        cat_name: '',
        // ID
        cat_pid: 0,
        // ,
        cat_level: 0
      },
      // 
      addCateFormRules: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      parentCateList: [],
      // 
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // ID
      selecteKeys: [],
      // 
      editCateDialogVisbel: false,
      editCate: {},
      editCateRules: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      editCateId: ''
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return res.$message.error(' !')
      }
      //   catelist
      this.catelist = res.data.result
      // 
      this.total = res.data.total
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    // pagenum 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },
    // ,
    showAddCateDialog() {
      // 
      this.getParentCateList()
      // 
      this.addCateDialogVisible = true
    },
    // 
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', { params: { type: 2 } })
      if (res.meta.status !== 200) {
        return res.$message.error(' !')
      }
      this.parentCateList = res.data
    },
    // 
    parentCateChanged() {
      console.log(this.selecteKeys)
      // selecteKeys0 
      // 
      if (this.selecteKeys.length > 0) {
        // ID
        this.addCateForm.cat_pid = this.selecteKeys[this.selecteKeys.length - 1]
        // 
        this.addCateForm.cat_level = this.selecteKeys.length
        return
      } else {
        // ID
        this.addCateForm.cat_pid = 0
        // 
        this.addCateForm.cat_level = 0
      }
    },
    // 
    addCate() {
      this.$refs.addCateFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) {
          return res.$message.error(' !')
        }
        this.$message.success(' !')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },
    //  
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields()
      this.selecteKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    },
    async showeditCateDialog(cateInfo) {
      this.editCateId = cateInfo.cat_id
      const { data: res } = await this.$http.get('categories/' + cateInfo.cat_id)
      this.editCate = res.data
      this.editCateDialogVisbel = true
    },
    // 
    async editCateInfo() {
      const { data: res } = await this.$http.put('categories/' + this.editCate.cat_id, { cat_name: this.editCate.cat_name })
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.getCateList()
      this.editCateDialogVisbel = false
      // console.log(res)
    },
    // 
    async removeCate(id) {
      const confirRustle = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch((err) => err)

      if (confirRustle !== 'confirm') {
        return this.$message.info('!')
      }

      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.getCateList()
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
}
.bottompage {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
</style>