<template>
  <div class="login-container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="/assets/logo.png" alt="">
      </div>
      <!--  -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" class="login_form">
        <!--  -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="el-icon-user-solid"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" type="password"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 10, message: ' 5  10 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields()
    },
    login() {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post('login', this.loginForm)
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        // 1. tokensessionStorage
        //  1.1 api,
        //  1.2 token,tokensessionStorage
        window.sessionStorage.setItem('token', res.data.token)
        // 2. , /home
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login-container {
  color: aqua;
  height: 100%;
  background-color: #ccc;
}
.login_box {
  width: 500px;
  height: 300px;
  background-color: whitesmoke;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .avatar_box {
    height: 130px;
    width: 130px;
    position: absolute;
    border-radius: 50%;
    padding: 10px;
    background-color: white;
    left: 50%;
    transform: translate(-50%, -50%);
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
}
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: flex-end;
}
</style>